import { useState } from "react";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { useStudentInstituteCharges } from "../../../../Hooks/ApiHooks/StudentProfile/student-manager-api";
import { slashDateFormat } from "../../../../Utils/Functions/dateUtility";
import PaymentGatewayConfig from "../../../../../PaymentGateway/PaymentGatewayConfig";
import dayjs from "dayjs";
import { useTranslation } from "react-i18next";
import style from "../../FinanceManager.module.css";
import FormInput from "../../../../Ui/Input/FormInput";
import FormCalendarInput from "../../../../Ui/Input/FormCalendarInput";
import PaymentModeType from "../../../PaymentModeType/PaymentModeType";
import FinanceOtherMiscellaneousStudentSearch from "./FinanceOtherMiscellaneousStudentSearch";
import BackdropModal from "../../../../Utils/Modal/BackdropModal";
import {
  assestsFeedUrl,
  assestsNavbarUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../../../Services/UrlConfig/BaseUrl";
import styleFeedback from "../../../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";

const FinanceOtherMiscellaneousNewReceipt = ({ instituteId }) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    amount: "",
    mode: "",
    fee_payment_mode: "",
    fee_payment_amount: "",
    transaction_date: dayjs().format("DD/MM/YYYY"),
    fee_bank_name: "",
    fee_bank_holder: "",
    fee_utr_reference: "",
  });
  const [disabled, setDisabled] = useState(false);
  const [errorField, setErrorField] = useState("");
  const [openPdf, setOpenPdf] = useState("");
  const [singleStudent, setSingleStudent] = useState(null);
  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });

  const [step, setStep] = useState("");

  const [pgstate, setPgstate] = useState({
    amount: 0,
    type: "Other",
    isTransaction: false,
  });

  const { studentInstituteCharges } = useStudentInstituteCharges({
    data: {
      id: instituteId,
    },
    skip: !instituteId,
  });
  const onCloseBackdrop = () => {
    setStep("");
  };
  const onInputAmount = (e) => {
    setState((pre) => ({
      ...pre,
      amount: +e.target.value,
      fee_payment_amount: +e.target.value,
    }));
    setPgstate((prev) => ({
      ...prev,
      amount: +e.target.value,
    }));
  };

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onTransactionDate = (val) => {
    setState((prev) => ({
      ...prev,
      transaction_date: slashDateFormat(val),
    }));
  };
  const onModeChange = (val) => {
    setState((prev) => ({
      ...prev,
      mode: "Online",
      fee_payment_mode: val,
    }));
  };

  const onSelectStudent = (dt) => {
    setSingleStudent(dt);
    onCloseBackdrop();
  };
  return (
    <>
      <div className={style.fm_search_full}>
        <section className={style.fm_search_full_container}>
          <div className={style.fm_search_full_container_inner}>
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onClick={() => setStep(1)}
            />
          </div>
        </section>
      </div>

      {step === 1 ? (
        <>
          <BackdropModal
            onBackdropClose={onCloseBackdrop}
            customBackdropStyle={{
              backgroundColor: "transparent",
            }}
          />

          <FinanceOtherMiscellaneousStudentSearch
            instituteId={instituteId}
            onCloseBackdrop={onCloseBackdrop}
            onSelectStudent={onSelectStudent}
          />
        </>
      ) : null}
      {singleStudent?._id ? (
        <div className={styleFeedback.gsf_contatiner}>
          <div className={styleFeedback.sf_card_contatiner}>
            <img
              src={
                singleStudent.studentProfilePhoto
                  ? `${imageShowUrl}/${singleStudent?.studentProfilePhoto}`
                  : `${assestsFeedUrl}/feed-user-avatar.svg`
              }
              loading="lazy"
              alt="avatar"
              style={{
                width: "2.5rem",
                height: "2.5rem",
              }}
            />

            <div className={styleFeedback.sf_card_contatiner_inner}>
              <h6>
                {`${singleStudent.studentFirstName ?? ""} ${
                  singleStudent.studentMiddleName ?? ""
                } ${singleStudent.studentLastName ?? ""}`}
              </h6>
              <p>
                {t("gr_number")} {": "}
                {singleStudent.studentGRNO ?? ""}
              </p>
            </div>
          </div>
        </div>
      ) : null}

      <div
        className={style.fm_header_start_container}
        style={{
          gap: "2%",
          flexWrap: "wrap",
          marginTop: "1rem",
        }}
      >
        <FormInput
          label={t("amount")}
          placeholder={t("amoute_details_collect")}
          name={"amount"}
          value={state.amount}
          type={"tel"}
          onChange={onInputAmount}
          errorShow={errorField["amount"]}
          msg={errorField["rest_issue"]}
          customFormInputContainer={{
            width: "49%",
          }}
        />
        <div
          style={{
            width: "49%",
            marginTop: "-0.8rem",
          }}
        >
          <PaymentModeType
            paymentHeading={t("payment_type_mode")}
            selectLabel={t("mode_placeholder")}
            onClick={onModeChange}
            errorShow={errorField["fee_payment_mode"]}
            defalutValue={state.fee_payment_mode}
            selectedValue={t("please_select_payment_mode")}
            isAuthority="admissionCollectModePg"
            customLabelStyle={{
              marginTop: "0",
              fontSize: "10px",
            }}
            customListStyle={{
              position: "absolute",
              width: "100%",
              zIndex: "1",
            }}
          />
        </div>

        {state.fee_payment_mode === "Payment Gateway" ? null : (
          <>
            <FormCalendarInput
              label={t("select_date_of_transaction")}
              placeholder={t("please_select_date")}
              name={"transaction_date"}
              value={state.transaction_date}
              onChange={onTransactionDate}
              errorShow={errorField["transaction_date"]}
              customFormInputContainer={{
                width: "49%",
              }}
            />
            {state.fee_payment_mode === "Cash" ? null : (
              <>
                <FormInput
                  label={t("bank_name_collect")}
                  placeholder={t("please_enter_bank_name")}
                  name={"fee_bank_name"}
                  value={state.fee_bank_name}
                  type={"text"}
                  onChange={onInputChange}
                  customFormInputContainer={{
                    width: "49%",
                  }}
                />
                <FormInput
                  label={t("banke_holder_name")}
                  placeholder={t("please_enter_name_of_bank_holder")}
                  name={"fee_bank_holder"}
                  value={state.fee_bank_holder}
                  type={"text"}
                  onChange={onInputChange}
                  customFormInputContainer={{
                    width: "49%",
                  }}
                />
                <FormInput
                  label={t("cheque_dd_neft")}
                  placeholder={t("please_enter_utr_ref_number")}
                  name={"fee_utr_reference"}
                  value={state.fee_utr_reference}
                  type={"text"}
                  onChange={onInputChange}
                  customFormInputContainer={{
                    width: "49%",
                  }}
                />
              </>
            )}
          </>
        )}
      </div>
      {state.fee_payment_mode === "Payment Gateway" ? (
        <CreateButton
          label="collect_fees"
          customStyle={{
            width: "100%",
          }}
        />
      ) : (
        <CreateButton
          label="collect_fees"
          customStyle={{
            width: "100%",
          }}
        />
      )}

      {disabled && <QvipleLoader />}
      {pgstate.isTransaction ? (
        <PaymentGatewayConfig
          onClose={() =>
            setPgstate((prev) => ({
              ...prev,
              isTransaction: false,
            }))
          }
          allContent={{
            ...pgstate,
            platform_charges:
              studentInstituteCharges?.platform_charges?.percentage,
          }}
          //   checkoutAdmissionHandler={onPgCheckoutHandler}
          isOptimzed
        />
      ) : null}
    </>
  );
};

export default FinanceOtherMiscellaneousNewReceipt;
